<template>
	<div class="box">
		<header-box></header-box>
		<div class="song-title">{{song.title}}</div>
		<div class="center" @click="playSong">
			<div class="bor837F7D">
				<img :style="{transform: rotate}" class="cover" :src="song.coverUrl" alt="">
				<img :style="{opacity: opacity}" class="play" src="../assets/imgs/icon/play.png" alt="">
			</div>
		</div>
		<div class="range">
			<div :style="{width: timeWidth}" class="had-line"></div>
		</div>
		<div class="text"><p v-html="texts"></p></div>
		<!-- <audio-box></audio-box> -->
	</div>
</template>

<script>
import Header from '../components/Header'
import AudioTem from '../components/AudioTem'
export default {
	name: 'play',
	data () {
		return {
			song: this.$route.params.data,
			audio: null,
			isPlay: false,
			opacity: 1,
			rotate: 'rotate(0deg)',
			imgRotate: null,
			rotateNum: 0,
			timeWidth: 0
		}
	},
	components: {
		headerBox: Header,
		audioBox: AudioTem
	},
	computed: {
		texts : function(){
			return this.song.description.replace(/\n/g, '<br/>')
		}
	},
	mounted () {
		console.log(this.audio);
		this.audio = document.createElement('audio');
		this.audio.src = this.song.fileOptions[1].url;
	},
	methods: {
		playSong: function(){
			this.isPlay = !this.isPlay;
			if(this.isPlay){
				this.audio.play();
				this.playBtnHide();
				this.imgRotateStart();
				this.timeRangeStart();
			}else{
				this.audio.pause();
				this.playBtnShow();
				this.imgRotateStop();
				this.timeRangeStop();
			}
		},
		playBtnHide: function(){
			var that = this;
			var t = setInterval(function(){
				that.opacity -= 0.1
				if(that.opacity<=0){
					clearInterval(t);
				}
			},20)
		},
		playBtnShow: function(){
			var that = this;
			var t = setInterval(function(){
				that.opacity += 0.1
				if(that.opacity>=1){
					clearInterval(t);
				}
			},20)
		},
		imgRotateStart: function(){
			var that = this;
			this.imgRotate = setInterval(function(){
				that.rotateNum += 0.1;
				that.rotate = 'rotate(' + that.rotateNum + 'deg)';
			},10)
		},
		imgRotateStop: function(){
			clearInterval(this.imgRotate)
		},
		timeRangeStart: function(){
			var that = this;
			this.times = setInterval(function(){
				that.timeWidth = Math.ceil(that.audio.currentTime/that.audio.duration*100) + '%';
			},10)
		},
		timeRangeStop: function(){
			clearInterval(this.times)
		}
	}
}
</script>

<style scoped>
.box{
	width: 100%;
}
.song-title{
	width: 100%;
	height: 2.5rem;
	line-height: 2.5rem;
	text-align: center;
	font-size: 1.5rem;
	margin-top: 2rem
}
.center{
	width: 100%;
	height: 13rem;
	padding-top: 0.5rem
}
.bor837F7D{
	width: 12.5rem;
	height: 12.5rem;
	border-radius: 50%;
	background: #000;
	margin: 0 auto;
	position: relative;
	border: 0.25rem solid #837F7D;
}
.cover{
	width: 9rem;
	height: 9rem;
	display: block;
	margin: 1.5rem auto;
	border-radius: 50%
}
.play{
	width: 4rem;
	height: 4rem;
	position: absolute;
	left: 4rem;
	top: 4rem;
}
.range{
	width: 80%;
	height: 0.1rem;
	margin: 3rem auto;
	background: #f0f0f0;
}
.had-line{
	width: 10%;
	height: 0.1rem;
	float: left;
	background: #0dd
}
.text{
	width: 80%;
	line-height: 1.1rem;
	font-size: 1rem;
	color: #333;
	margin: 0 auto;
}
.hide{
	display: none;
}
.show{
	display: block
}
</style>


