import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/page/index'
import Play from '@/page/play'

Vue.use(Router)

export default new Router({
	routes: [
		{
			path: '/',
			name: 'index',
			component: Index
		}, 
		{
			path: '/play',
			name: 'play',
			component: Play
		}
	]
})


